<template>
    <div class="img-back" style="position: relative; height: 100%!important;">
        <v-container class="login" style="position: relative; padding-bottom: 40px; height: 100%; max-width: 550px">
            <v-col cols="12" style="padding-top: 0px">
                <v-card-title class="text-center justify-center pa-3 white--text">
                    {{createYourAccountText}}
                </v-card-title>

                <v-card style="padding-bottom: 8px" class="text--center justify-center">
                    <div align="center" style="padding-top: 24px">
                        <img style="width: 60px" src="/LogoWeBabble.png"/>
                    </div>

                    <v-container class="pa-3" style="padding-top: 0px!important;">
                        <v-form v-model="validForm" class="pa-1">
                            <v-text-field
                                    style="padding-top: 0px"
                                    v-model="name"
                                    :rules="[rules.required]"
                                    :label="nameText"
                                    required
                            ></v-text-field>

                            <v-text-field
                                    v-model="lastName"
                                    :rules="[rules.required]"
                                    :label="lastNameText"
                                    required
                            ></v-text-field>


                            <v-text-field
                                    v-model="email"
                                    :rules="[rules.required, rules.email]"
                                    :label="emailText"
                                    required
                            ></v-text-field>

                            <v-text-field
                                    ref="password"
                                    v-model="password"
                                    :rules="[rules.required, rules.min8Char, rules.samePassword]"
                                    :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                                    :type="showPassword ? 'text' : 'password'"
                                    :label="passwordText"
                                    :hint="min8CharText"
                                    counter
                                    @click:append="showPassword = !showPassword"
                                    required
                            ></v-text-field>

                            <v-text-field
                                    ref="password2"
                                    v-model="password2"
                                    :rules="[rules.required, rules.min8Char, rules.samePassword]"
                                    :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                                    :type="showPassword ? 'text' : 'password'"
                                    :label="repeatPasswordText"
                                    :hint="min8CharText"
                                    counter
                                    @click:append="showPassword = !showPassword"
                                    required
                            ></v-text-field>
                        </v-form>

                        <v-btn block color="primary" class="white--text" @click="signUp" :disabled="buttonIsDisabled"
                               style="margin-top: 10px">
                            {{registerText}}
                        </v-btn>
                    </v-container>
                </v-card>
            </v-col>

            <v-col class="pa-0" cols="12" align="center" id="sign-up-now-claim"
                   style="position: absolute; bottom: 0px; margin-left: -12px">
                <p class="white--text" style="font-size: 14px">
                    {{alreadyHaveAnAccountText}}
                    <router-link to="/login" class="white--text" style="font-size: 14px">
                        {{accessNowText}}
                    </router-link>
                </p>
            </v-col>

            <v-snackbar
                    v-model="snackbar"
                    :timeout="4000"
            >
                {{ snackbarText }}
                <v-btn
                        color="primary"
                        text
                        @click="snackbar = false"
                >
                    {{closeText}}
                </v-btn>
            </v-snackbar>
        </v-container>
    </div>
</template>

<script>
    import {mapActions} from 'vuex';

    // Página de registro con formulario para la creación de una cuenta en la aplicación
    // @group Cuenta
    export default {
        name: "SignUp",
        data() {
            return {
                validForm: false,
                name: '',
                lastName: '',
                email: '',
                password: '',
                password2: '',
                showPassword: false,
                buttonIsDisabled: true,
                correctPasswordCombination: false,
                snackbar: false,
                snackbarText: '',
                rules: {
                    required: value => !!value || this.$i18n.t('messages.RULE_TEXT_MANDATORY_FIELD'),

                    min8Char: value => (value.length >= 8 || value.length == 0) || this.$i18n.t('messages.RULE_TEXT_MIN_VALUE_CHARACTERS_PASSWORD'),
                    email: value => {
                        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                        return pattern.test(value) || this.$i18n.t('messages.TEXT_EMAIL_NOT_VALID')
                    },

                    samePassword: () => this.correctPasswordCombination == true || this.$i18n.t('messages.ERROR_PASSWORDS_DONT_MATCH')
                },
                closeText: null,
                createYourAccountText: null,
                nameText: null,
                lastNameText: null,
                emailText: null,
                passwordText: null,
                repeatPasswordText: null,
                registerText: null,
                alreadyHaveAnAccountText: null,
                accessNowText: null,
                min8CharText: null
            }
        },
        methods: {
            ...mapActions('user', ['createAccount']),

            // @vuese
            // Crea la cuenta del usuario con los datos introducidos y, si el registro procede sin errores, lo redirige al siguiente paso en el proceso de registro
            signUp: function () {
                let self = this;
                //Create firebase user
                let resultCreateAccount = this.createAccount({
                    'name': this.name,
                    'lastName': this.lastName,
                    'email': this.email,
                    'password': this.password
                });

                resultCreateAccount.then(() => {
                    self.$router.push('email-verification');
                })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            }
        },
        created() {
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.createYourAccountText = this.$i18n.t('messages.TEXT_CREATE_YOUR_ACCOUNT');
            this.nameText = this.$i18n.t('messages.TEXT_NAME');
            this.lastNameText = this.$i18n.t('messages.TEXT_LASTNAME');
            this.emailText = this.$i18n.t('messages.TEXT_EMAIL');
            this.passwordText = this.$i18n.t('messages.TEXT_PASSWORD');
            this.repeatPasswordText = this.$i18n.t('messages.TEXT_REPEAT_PASSWORD');
            this.registerText = this.$i18n.t('messages.TEXT_REGISTER');
            this.alreadyHaveAnAccountText = this.$i18n.t('messages.TEXT_ALREADY_HAVE_AN_ACCOUNT');
            this.accessNowText = this.$i18n.t('messages.TEXT_ACCESS_NOW');
            this.min8CharText = this.$i18n.t('messages.RULE_TEXT_MIN_VALUE_CHARACTERS_PASSWORD');
        },
        watch: {
            validForm: function (value) {
                if (value) {
                    this.buttonIsDisabled = false
                } else {
                    this.buttonIsDisabled = true
                }
            },

            // @vuese
            // Observa el campo 'Contraseña' y cuando este se actualiza, lo compara con el de 'Repetir contraseña'
            password: function (value) {
                if (value === this.password2) {
                    this.correctPasswordCombination = true;
                    this.$refs.password2.validate();
                } else {
                    this.correctPasswordCombination = false;
                }
            },

            // @vuese
            // Observa el campo de 'Repetir contraseña' y cuando este se actualiza, lo compara con el de 'Contraseña'
            password2: function (value) {
                if (value === this.password) {
                    this.correctPasswordCombination = true;
                    this.$refs.password.validate();
                } else {
                    this.correctPasswordCombination = false;
                }
            }
        }
    }
</script>

